
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { mapState } from 'vuex'
import { JDGetScrapList, JDGetEmployeeInfoByOpenID, JDGetEmpAndLiableByEmpCode, JDInsertIntoFACardRect } from '@/api/getData'
import { setTimeout } from 'timers';
import Exif from 'exif-js'
export default {
data() {
return {
data: [['使用年限过长，功能丧失，无法使用需淘汰（报废）', '设备性能差，耗能低，效率低，不能满足使用需求（报废）', '严重损坏，无修复价值（报废）', '长期闲置（15个月以上），今后不会再使用（报废）', '出售']],

showPopupPicker: false,
showPopupPicker1: false,
FCardIDList: [],
resultList: [],
userInfo: {},
superviser: '',
value5: [],

FACardRectJson: {
FACardRectJson: []
},
FPicture:'',
picValue:''
};
},
computed:{
...mapState({
scrapList: state => state.mutations.scrapList,
wxOpenId: state => state.mutations.wxOpenId,
info: state => state.mutations.userInfo
})
},
components: {
XButton,
PopupPicker
},
mounted() {
this.scrapList.forEach(element => {
this.FCardIDList.push(element.FinterID)
});
console.log(this.scrapList)
this.getScrapList()
this.getUserInfo()
this.getSupervise() 
},
methods: {
async confirm() {

let obj = {
FInterID: '',
FNumber : '',
FDate: '',
FCardInterID: '',
FQty: '',
FReJect_Qty: '',
FReJect_fee: '',
FReJect_RevAmount: '',
FReJect_Reason: '',
FNote: '',
FRectEmpID: '',
FBillerID: '',
FBillDate: '',
FCheckerID: '',
FCheckDate: '',
FOARequestID: ''
}
this.resultList.forEach((item) => {
obj.FInterID = item.FInterID
obj.FNumber = item.FNumber
obj.FDate = new Date().getFullYear() + '-' +  (new Date().getMonth() + 1) + '-' + new Date().getDate()
obj.FCardInterID = item.FCardID
obj.FQty = item.FQty
obj.FReJect_Qty = item.FReJect_Qty
obj.FReJect_fee = '1'
obj.FReJect_RevAmount = '1'
obj.FReJect_Reason = this.value5[0]
obj.FNote = '1'
obj.FRectEmpID = this.userInfo.FitemID
obj.FBillerID = '1'
obj.FBillDate = '1'
obj.FCheckerID = '1'
obj.FCheckDate = '1'
obj.FOARequestID = '1'
obj.FPicture=this.FPicture.replace(/.*;base64,/, "")
this.FACardRectJson.FACardRectJson.push(obj)
obj = Object.assign({}, {})
})
this.FACardRectJson.FACardRectJson = JSON.stringify(this.FACardRectJson.FACardRectJson)
console.log(this.FACardRectJson)
this.$store.commit("updateLoadingStatus", { isLoading: true });
let data = await JDInsertIntoFACardRect(this.FACardRectJson)
console.log(data)
this.$vux.toast.text(data)
this.$store.commit("updateLoadingStatus", { isLoading: false });
setTimeout(() => {
this.$router.go(-1)
}, 800)

},
change() {

},
async getSupervise() {
let data = await JDGetEmpAndLiableByEmpCode({EmpCode: this.info[0].Fnumber})
this.superviser = data
console.log(data)
},
async getUserInfo() {
let data = await JDGetEmployeeInfoByOpenID({FWeixinOpenID: this.wxOpenId})
console.log(data)
this.userInfo = eval(data)[0]
},
async getScrapList() {
let data = await JDGetScrapList({CardIDList: this.FCardIDList.join(',')})
this.resultList = eval(data)
console.log(data)
},
upload (e) {
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.picValue = files[0];
this.imgPreview(this.picValue);
},
imgPreview (file) {
let self = this;
let Orientation;

Exif.getData(file, function(){
Orientation = Exif.getTag(this, 'Orientation');
});

if (!file || !window.FileReader) return;

if (/^image/.test(file.type)) {

let reader = new FileReader();

reader.readAsDataURL(file);

reader.onloadend = function () {
let result = this.result;
let img = new Image();
img.src = result;

if (this.result.length <= (100 * 1024)) {
self.FPicture = this.result;
self.postImg();
}else {
img.onload = function () {
let data = self.compress(img,Orientation);
self.FPicture = data;
self.postImg();
}
}
}
}
},
postImg () {

},
rotateImg (img, direction,canvas) {

const min_step = 0;
const max_step = 3;
if (img == null)return;

let height = img.height;
let width = img.width;
let step = 2;
if (step == null) {
step = min_step;
}
if (direction == 'right') {
step++;

step > max_step && (step = min_step);
} else {
step--;
step < min_step && (step = max_step);
}

let degree = step * 90 * Math.PI / 180;
let ctx = canvas.getContext('2d');
switch (step) {
case 0:
canvas.width = width;
canvas.height = height;
ctx.drawImage(img, 0, 0);
break;
case 1:
canvas.width = height;
canvas.height = width;
ctx.rotate(degree);
ctx.drawImage(img, 0, -height);
break;
case 2:
canvas.width = width;
canvas.height = height;
ctx.rotate(degree);
ctx.drawImage(img, -width, -height);
break;
case 3:
canvas.width = height;
canvas.height = width;
ctx.rotate(degree);
ctx.drawImage(img, -width, 0);
break;
}
},
compress(img,Orientation) {
let canvas = document.createElement("canvas");
let ctx = canvas.getContext('2d');

let tCanvas = document.createElement("canvas");
let tctx = tCanvas.getContext("2d");
let initSize = img.src.length;
let width = img.width;
let height = img.height;

let ratio;
if ((ratio = width * height / 4000000) > 1) {
console.log("大于400万像素")
ratio = Math.sqrt(ratio);
width /= ratio;
height /= ratio;
} else {
ratio = 1;
}
canvas.width = width;
canvas.height = height;

ctx.fillStyle = "#fff";
ctx.fillRect(0, 0, canvas.width, canvas.height);

let count;
if ((count = width * height / 1000000) > 1) {
console.log("超过100W像素");
count = ~~(Math.sqrt(count) + 1); 

let nw = ~~(width / count);
let nh = ~~(height / count);
tCanvas.width = nw;
tCanvas.height = nh;
for (let i = 0; i < count; i++) {
for (let j = 0; j < count; j++) {
tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
}
}
} else {
ctx.drawImage(img, 0, 0, width, height);
}

if(Orientation != "" && Orientation != 1){
switch(Orientation){
case 6://需要顺时针（向左）90度旋转
this.rotateImg(img,'left',canvas);
break;
case 8://需要逆时针（向右）90度旋转
this.rotateImg(img,'right',canvas);
break;
case 3://需要180度旋转
this.rotateImg(img,'right',canvas);
this.rotateImg(img,'right',canvas);
break;
}
}

let ndata = canvas.toDataURL('image/jpeg', 0.1);
console.log('压缩前：' + initSize);
console.log('压缩后：' + ndata.length);
console.log('压缩率：' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
console.info(ndata)
return ndata;
}
}
};



